<template>
   <div data-app class="col-12" v-if="ready">
       <label class='col-6'>Action Log</label>
            <div class='col-12'>
                <v-data-table
                :headers="headers"
                :items="loglist"
                class="elevation-1"
                :rows-per-page-items="[20]"
                >
                <template v-slot:items="props">
                    <td>{{ props.item.id }}</td>
                    <td>{{ props.item.date }} - {{ props.item.time }}</td>
                    <td>{{ props.item.action_l }}</td>                    
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>        
                </template>
                </v-data-table><br/>
            </div>
            <!--member--> 
</template>

<script>
export default {
    data(){
        return{
            loglist:[],
            ready:false,
            headers: [
                { text: 'ID',value :'id',sortable: true},
                { text: 'Date',value:'date',sortable: true},
                { text: 'Log',value:'action_l',sortable: false},          
            ], 
        }
    },
    async created(){
        await axios.get('Procurement/shared_backend/check_user_setup.php')
            .then(
                (response)=>{
                    this.user_check = response.data[0].signed;
                    var perm = response.data[0];
                    if(!(this.user_check=='set')){
                        this.$router.push('../user_setup');
                    }
                    if(perm.proc_level<4){
                        this.$alert("No permission").then(
                            ()=>{
                                this.$router.go(-1);
                            }
                        )
                    }
                }
            )
        await axios.get('Procurement/admin/backend/fetch_log.php')
        .then(
            (response)=>{
                this.loglist = response.data;           
            }
        )
        this.ready = true;
    }
}
</script>



